/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
header,
div {
  box-sizing: border-box;
}

.comp-header {
  position: fixed;
  width: 100%;
  top: 0;
  padding: 0;
  z-index: 1000;
  background-color: #fff;
  height: 72px;
  border-bottom: solid #000 2px;
  padding: 0 20px; }
  .comp-header .title-box {
    float: left;
    font-size: 20px; }
    .comp-header .title-box .nav-link {
      margin: 0 10px;
      display: inline-block;
      line-height: 70px;
      font-size: 20px; }
  .comp-header .nav-box {
    float: right; }
    .comp-header .nav-box .nav-link-box {
      display: inline-block;
      -webkit-transition: 0.3s !important;
      transition: 0.3s !important;
      color: #000; }
      .comp-header .nav-box .nav-link-box .nav-link {
        margin: 0 10px;
        display: inline-block;
        line-height: 70px;
        font-size: 20px;
        letter-spacing: 1px; }
      .comp-header .nav-box .nav-link-box:hover {
        color: #808080; }

/* Make clicks pass-through */
#nprogress {
  pointer-events: none; }

#nprogress .bar {
  background: #000;
  position: fixed;
  z-index: 1031;
  top: 0px;
  left: 0;
  width: 100%;
  height: 2px; }

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 10px #000, 0 0 5px #000;
  opacity: 1;
  -webkit-transform: rotate(0deg) translate(0px, 0px);
  transform: rotate(0deg) translate(0px, 0px); }

.nprogress-custom-parent {
  overflow: hidden;
  position: relative; }

.nprogress-custom-parent #nprogress .bar {
  position: absolute; }

