.comp-header {
  position: fixed;
  width: 100%;
  top: 0;
  padding: 0;
  z-index: 1000;
  background-color: #fff;
  height: 72px;
  border-bottom: solid #000 2px;
  padding: 0 20px;
  .title-box {
    float: left;
    font-size: 20px;
    .nav-link {
      margin: 0 10px;
      display: inline-block;
      line-height: 70px;
      font-size: 20px;
    }
  }
  .nav-box {
    float: right;
    .nav-link-box {
      display: inline-block;
      transition: 0.3s !important;
      color: #000;
      .nav-link {
        margin: 0 10px;
        display: inline-block;
        line-height: 70px;
        font-size: 20px;
        letter-spacing: 1px;
      }
      &:hover {
        color: #808080;
      }
    }
  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #000;

  position: fixed;
  z-index: 1031;
  top: 0px;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 10px #000, 0 0 5px #000;
  opacity: 1;

  -webkit-transform: rotate(0deg) translate(0px, 0px);
  -ms-transform: rotate(0deg) translate(0px, 0px);
  transform: rotate(0deg) translate(0px, 0px);
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
